<template>
	<view class="classlist">
		<view class="content">
			<navigator url="/pages/preview/preview" :key="i._id" class="item" v-for="i in classList">
				<image :src="i.smallPicurl" mode="aspectFill"></image>
			</navigator>
		</view>
		
	</view>
</template>

<script setup>
import { ref } from 'vue';
import {apiGetClassList} from '@/api/apis.js'
import { onLoad } from '@dcloudio/uni-app'

const classList = ref([])
const queryParams = {}

onLoad((options) => {
  // options 中包含所有传递过来的参数
  let {id=null,name=null} = options
  queryParams.classid = id
  console.log('接收的参数id:', options)
  uni.setNavigationBarTitle({
  	title:name
  })
  //onload晚于setup
  getClassList()
})

const getClassList = async()=>{
	let res = await apiGetClassList(queryParams)
	classList.value = res.data
	console.log(res)
}



</script>

<style lang="scss" scoped>
.classlist{
	.content{
		display: grid;
		grid-template-columns: repeat(3,1fr);
		gap: 5rpx;
		padding: 5rpx;
		.item{
			height: 440rpx;
			image{
				width: 100%;
				height: 100%;
				display: block;
			}
		}
	}
}     
</style>
